@import "config";
@import "tags";

* {
  box-sizing: border-box;
}

body {
  //@include background;
  background: $primary-color;
  color: set-text-color($primary-color);
  height: 100%;
  margin: 0;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.5;
   &::before {
      content: "";
      display: block;
      position: fixed;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: -10;
      background: url(../img/background.jpeg) no-repeat center center;
      -webkit-background-size: cover;
      -moz-background-size: cover;
      -o-background-size: cover;
      background-size: cover;
   }
}

// Headings
h1,
h2,
h3 {
  margin: 0;
  font-weight: 400;

  &.lg-heading {
    font-size: 6rem;
    text-shadow: 0px 1px 3px rgba(0, 0, 0, 1), 0px 1px 15px rgba(0, 0, 0, 1);
  }

  &.sm-heading {
    margin-bottom: 2rem;
    padding: 0.2rem 1rem;
    background: rgba(lighten($primary-color, 2), 0.5);
    text-shadow: 0px 1px 15px rgba(0, 0, 0, 1);
  }
}

a {
  color: #fff;
  text-decoration: none;
  transition: all 320ms ease;
}

.break {
  display: none;
}

.text-secondary {
  color: $secondary-color;
}

//Header
.header {
  height: 100vh;
  padding: 1rem;
  &-logo {
    margin-top: 3rem;
    .portrait {
      margin: 0 auto;
      width: 250px;
      height: 250px;
      background: url('../img/portrait.png');
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
    }    
  }

  .icons {
    margin-top: 1rem;
    a {
      padding: 0.4rem;
      text-shadow: 0px 1px 10px rgba(0, 0, 0, 1);

      &:hover {
        color: $secondary-color;
        text-shadow: 0px 1px 3px rgba(0, 0, 0, 1);
        @include easeOut();
      }
    }
  }
  .projects-header {
    position: relative;
    z-index: 3;
    .sm-heading {
      padding: 1rem;
      color: #000;
      background: none;
      text-align: center;
      
    }
  }
}

//Main
.main {
  background-color: #fff;
  padding: 4rem;
  //height: 100%;
  min-height: calc(100vh - 60px); //Item above footer - footer height!
}

//Projects
.section {
  &-heading {
    color: $secondary-color;
    font-size: 1.5rem;
    z-index: 3;
    position: relative;
    top: -8.5rem;
    text-align: center;
    cursor: pointer;
    text-shadow: 1px 2px 1px rgba(0, 0, 0, 1);
  }
}
.projects-heading {
  height: 0;
  position: absolute;
  width: 0;
  border-left: 25vw solid transparent;
  border-right: 25vw solid transparent;
  border-bottom: 100px solid #fff;
  z-index: 2;
  margin-top: -100px;
  left: 50%;
  transform: translate(-50%, -50%);
}
.projects {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
  .item {
    background-color: #c4c4c4;
    padding: 15px;
    border-radius: 5px;
    margin-bottom: 5vh;
    box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.5);
    &-box {
      color: #333;
      display: block;
      padding: 0.5rem 1rem;
      border: 0;
      margin-bottom: 0.3rem;
    }
    &-title {
      color: #000;
      font-weight: 600;
      font-size: 1.2rem;
      margin: 0;
    }
    &-desc {
      margin: 0;
    }
    &-tech {
      margin-bottom: 0;
    }
  }

  img {
    width: 100%;
    border-radius: 5px;
    box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.5);
    &:hover {
      opacity: 0.5;
      border-color: $secondary-color;
      @include easeOut();
    }
  }
}

//Buttons
.btn {
  display: inline-block;
  padding: 0.5rem 1rem;
  border: 0;
  margin: 0.4rem;
  width: 40%;
  border-radius: 5px;
  white-space: nowrap;

  &:hover {
    background: $secondary-color;
    color: set-text-color($secondary-color);
  }
}

.btn-dark {
  @extend .btn;
  background: #000;
  color: #fff;
  box-shadow: 0px 1px 1px 0px rgba(0,0,0,0.5);
}
.btn-light {
  @extend .btn;
  background: #fff;
  color: #000;
  box-shadow: 0px 1px 1px 0px rgba(0,0,0,0.75);
}
//Other stuff
.other {
  &-heading {
    color: #000;
    font-size: 2rem;
  }
  &-stuff {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    a {
      color: #000;
      display: block;
      margin: 5px;
      padding: 5px;
      border: 1px solid #000;
    }
  }
}
//Knowledge
.knowledge {
  &_button {
    font-size: 1.4rem;
    padding: 1rem;
    margin-bottom: 3rem;
    cursor: pointer;
    background: $secondary-color;
    box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.5);
    a {
      color: #000;
    }
  }
}
//Footer

.footer {
  text-align: center;
  padding: 1rem;
  background: darken($primary-color, 10);
  color: set-text-color($primary-color);
  height: auto;
}

//Media querries
@import "mobile";